import React, { useState, useEffect } from "react";
import "./css/ProjectMain.css";
import DatePicker from "react-datepicker";
import { NavLink } from "react-router-dom";
import "react-datepicker/dist/react-datepicker.css";
import { format, subDays } from "date-fns";

function ProjectMain() {
  const API_KEY =
    "test_381cc05b96e9ee7a1875549818bf3685bd2f4d3940406a80165bcfd6df0b2afbc650abfc9d0f6a57bc6b7bdf91c32093";
  const [selectedDate, setSelectedDate] = useState<Date>(
    subDays(new Date(), 1)
  );
  const year = selectedDate.getFullYear();
  const month = String(selectedDate.getMonth() + 1).padStart(2, "0");
  const day = String(selectedDate.getDate() - 1).padStart(2, "0");
  const formattedDate = `${year}-${month}-${day}`;
  const [state, setState] = useState<boolean>(false);
  const [characterOcid, setCharacterOcid] = useState<string>("");
  const [urlString, setUrlString] = useState<string>("");
  const [characterName, setCharacterName] = useState<string>("");
  const [characterInfo, setCharacterInfo] = useState<any>(null); // :  null 
  const [buttonClicked, setButtonClicked] = useState<boolean>(false);

  useEffect(() => {
    if (urlString !== "") {
      getBasicInfo(characterOcid, format(selectedDate, "yyyy-MM-dd"));
    }
  }, [urlString, characterOcid, formattedDate]);

  useEffect(() => {
    if (buttonClicked && characterName !== "") {
      setCharacterCode();
      setButtonClicked(false);
    }
  }, [buttonClicked, characterName]);

  function setCharacterCode() {
    const url = `https://open.api.nexon.com/maplestory/v1/id?character_name=${characterName}`;

    setUrlString(url);

    fetch(url, {
      headers: {
        "x-nxopen-api-key": API_KEY,
      },
    })
      .then((response) => response.json())
      .then((data) => setCharacterOcid(data.ocid))
      .catch((error) => console.error(error));
  }

  function getBasicInfo(characterOcid: string, date: string) {
    console.log("characterOcid : ", characterOcid);
    console.log("date : ", date);
    const url = `https://open.api.nexon.com/maplestory/v1/character/basic?ocid=${characterOcid}&date=${date}`;

    fetch(url, {
      headers: {
        "x-nxopen-api-key": API_KEY,
      },
    })
      .then((response) => response.json())
      .then((data) => setCharacterInfo(data)) // : JSON.stringify  
      .catch((error) => console.error(error));
  }

  function stateChange() {
    setButtonClicked(true);
    setState(!state);
  }

  return (
    <div className="ProjectMain" style={{ textAlign: "left" }}>
      <div className="input-group mb-3">
        <span className="input-group-text" id="basic-addon1">
          
        </span>
        <input
          type="text"
          className="form-control"
          placeholder=" "
          aria-label="Username"
          aria-des
          onChange={(event) => setCharacterName(event.target.value)}
        />
      </div>
      <br />
      <div>
        <DatePicker
          className="react-datepicker__triangle"
          dateFormat="yyyy-MM-dd" //  
          shouldCloseOnSelect //   datepicker  
          maxDate={subDays(new Date(), 1)} // maxDate    
          selected={selectedDate}
          onChange={(date) => setSelectedDate(date != null ? date : new Date())}
        />
      </div>
      <br />
      <div>
        <button onClick={stateChange}></button>
      </div>
      <br />
      {characterInfo ? (
        <div>
           : {characterInfo.world_name}
          <br />
           : {characterInfo.character_class}
          <br />
           : {characterInfo.character_level}
          <br />
           : {characterInfo.character_exp}
          <br />
           : {characterInfo.character_guild_name}
          <br />
          <img src={characterInfo.character_image} alt="" />
        </div>
      ) : (
        "    "
      )}
    </div>
  );
}

export default ProjectMain;
